@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700);
main {
  flex-grow: 1;
}
main > * {
  margin: 40px auto;
  max-width: 800px;
}

.home_header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-grow: 0;
  flex-shrink: 0;
  height: 250px;
  background: var(--black);
  color: var(--white);
}
.home_header > svg {
  margin-bottom: 5px;
}
.title_big {
  font-size: 2em;
}

.link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 30px;
  min-height: 30px;
  color: var(--black);
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  text-decoration: none;
  cursor: pointer;
  outline: none;
  font-size: 11pt;
  -webkit-transition: color var(--fast), background var(--fast), opacity var(--fast),
  border var(--fast), border-color var(--fast);
  transition: color var(--fast), background var(--fast), opacity var(--fast),
  border var(--fast), border-color var(--fast);
}
.link:hover,
.link:focus {
  color: var(--green);
}
.link > svg:not(:first-child) {
  margin-left: 5px;
}
.link > svg:not(:last-child) {
  margin-right: 5px;
}
.link[data-text='true'] {
  margin: 0 10px;
  padding: 0 10px;
  color: var(--green);
  font-weight: 500;
  border: solid 2px transparent;
  outline: none;
}
.link[data-text='true']:hover,
.link[data-text='true']:focus {
  border-color: var(--green);
  outline: none;
}

.banner {
  text-align: center;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}
.banner > .link {
  margin-top: 20px;
}

.feature {
  display: flex;
  justify-content: center;
  align-items: center;
}
.pane {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50%;
  padding: 0 10px;
}
.video {
  width: 320px;
  height: 180px;
  max-width: 100%;
  max-height: 100%;
  background: var(--dark-gray);
}
.feature_header > svg {
  margin-right: 10px;
}

@media screen and (max-width: 640px) {
  .feature {
    flex-direction: column;
  }
  .pane {
    width: 100%;
    padding: 0;
  }
  .pane_first {
    order: -1;
    margin-bottom: 20px;
  }
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  box-shadow: var(--shadow);
}

.horizontal_line {
  content: '';
  width: 100%;
  border: none;
  height: 1px;
  background: var(--light-gray);
  z-index: -1;
}


.logo_small {
  color: var(--green);
  margin-right: 10px;
}

.nav_button {
  position: relative;
  margin: 5px;
  padding: 0 5px;
  height: 30px;
}
.nav_button > svg:not(:last-child) {
  margin-right: 10px;
}
.nav_button:after {
  content: '';
  position: absolute;
  left: 50%;
  right: 50%;
  bottom: 0;
  height: 2px;
  opacity: 0;
  background: var(--green);
  -webkit-transition: left var(--fast), right var(--fast), opacity var(--fast);
  transition: left var(--fast), right var(--fast), opacity var(--fast);
}
.nav_button:hover:after,
.nav_button:focus:after,
.nav_button[data-active='true']:after {
  left: 0;
  right: 0;
  opacity: 1;
  background: var(--green);
}


.tooltip {
  position: absolute;
  max-width: 180px;
  padding: 5px 10px;
  background: var(--black);
  color: var(--white);
  z-index: 99;
}
.tooltip-enter {
  opacity: 0;
}
.tooltip-enter-active {
  opacity: 1;
  -webkit-transition: opacity var(--fast);
  transition: opacity var(--fast);
}
.tooltip-exit {
  opacity: 1;
}
.tooltip-exit-active {
  opacity: 0;
  -webkit-transition: opacity var(--fast);
  transition: opacity var(--fast);
}

button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 30px;
  min-height: 30px;
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  text-decoration: none;
  cursor: pointer;
  outline: none;
  font-size: 11pt;
  -webkit-transition: color var(--fast), background var(--fast), opacity var(--fast),
  border var(--fast), border-color var(--fast);
  transition: color var(--fast), background var(--fast), opacity var(--fast),
  border var(--fast), border-color var(--fast);
}
button:hover,
button:focus {
  color: var(--green);
}
button > svg:not(:first-child) {
  margin-left: 5px;
}
button > svg:not(:last-child) {
  margin-right: 5px;
}
button[data-text='true'] {
  margin: 0 10px;
  padding: 0 10px;
  color: var(--green);
  font-weight: 500;
  border: solid 2px transparent;
  outline: none;
}
button[data-text='true']:hover,
button[data-text='true']:focus {
  border-color: var(--green);
  outline: none;
}

.popup_overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #00000080;
  z-index: 99;
}
.popup_content {
  background: var(--white);
  position: absolute;
  box-shadow: var(--shadow);
}

.field {
  box-sizing: border-box;
  display: inline-block;
  padding: 0 5px;
}
.nowrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


.model_item {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  width: 100%;
  height: 40px;
}
.model_button {
  display: inline-flex;
  justify-content: flex-start;
  align-items: stretch;
  width: calc(100% - 30px);
}
.model_radio {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 30px;
}
.model_summary {
  display: inline-flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  width: calc(100% - 30px);
}
.model_summary > * {
  line-height: 1em;
}


.model_select_button {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  background: var(--off-white);
  border-radius: 100%;
}
.model_select_button:hover,
.model_select_button:focus {
  background: var(--green);
  color: var(--white);
}
.model_select_popup {
  width: 280px;
  min-height: calc((1 * 40px) + ((1 - 1) * 1px));
  max-height: calc((5 * 40px) + ((5 - 1) * 1px));
  overflow-x: hidden;
  overflow-y: auto;
}
.model_alert {
  height: 40px;
}


.page_header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  box-shadow: var(--shadow);
}
.page_header a {
  text-decoration: none;
}
.page_header_column {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  height: 50px;
}
.page_header_column:nth-child(1) {
  justify-content: flex-start;
  padding: 0 10px;
}
.page_header_column:nth-child(2) {
  justify-content: center;
  flex-grow: 1;
}
.page_header_column:nth-child(3) {
  justify-content: flex-end;
  padding: 0 10px;
}

@media screen and (max-width: 650px) {
  .page_header_column:nth-child(1) {
    flex-grow: 1;
    order: 1;
  }
  .page_header_column:nth-child(2) {
    flex-direction: column;
    flex-basis: 100%;
    flex-grow: 0;
    height: unset;
    order: 3;
  }
  .page_header_column:nth-child(3) {
    flex-grow: 1;
    order: 2;
  }
  .page_header_column:first-child:last-child {
    justify-content: center;
  }
}

.section_header {
  color: var(--gray);
  display: flex;
  justify-content: center;
  align-items: center;
}
.section_header:before,
.section_header:after {
  content: '';
  flex-grow: 1;
  background: var(--light-gray);
  height: 1px;
}
.section_header:before {
  margin-right: 20px;
}
.section_header:after {
  margin-left: 20px;
}

.input {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  width: 100%;
  height: 30px;
  padding: 2px 0;
}
.input[data-expanded='true'] {
  height: calc(5 * 20px);
}
.input:before,
.input:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: -10;
  pointer-events: none;
  -webkit-transition: border-color var(--fast);
  transition: border-color var(--fast);
}
.input:before {
  border: solid 2px transparent;
}
.input[data-expanded='true']:before {
  border-color: var(--off-white);
}
.input:after {
  border-bottom: solid 2px var(--black);
}
.input[data-focused='true']:after {
  border-color: var(--green);
}
input,
textarea {
  box-sizing: border-box;
  flex-basis: 0;
  min-width: 0;
  flex-grow: 1;
  height: 100%;
  padding: 0 7.5px;
  background: none;
  border: none;
  outline: none;
  font-size: 11pt;
  resize: none;
  line-height: 20px;
}
input:focus,
textarea:focus {
  outline: none;
}
.input[data-expanded='true'] > textarea {
  font-size: 10pt;
}
.input_button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
}

.search_results {
  width: 100%;
  margin-top: 10px;
  max-height: calc((6 * 30px) + ((6 - 1) * 1px));
  overflow-x: auto;
  overflow-y: auto;
}
.search_results > * {
  min-width: 500px;
}

.gene_search_result_single {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  height: 30px;
}
.gene_search_result_single_button {
  display: inline-flex;
  justify-content: flex-start;
  align-items: stretch;
  width: calc(100% - 30px);
  text-align: left;
}
.gene_search_result_single_check {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 30px;
  width: 30px;
}
.gene_search_result_single_summary {
  width: calc(100% - 30px);
}
.gene_search_result_single .field:nth-child(1),
.gene_search_result_single .field:nth-child(2),
.gene_search_result_single .field:nth-child(3) {
  width: 15%;
}
.gene_search_result_single .field:nth-child(4) {
  width: 55%;
}

.gene_search_results_single_alert {
  height: calc(2 * 30px);
}

.vertical_line {
  content: '';
  width: 1px;
  height: calc(100% - 5px - 5px);
  margin: 5px 0;
  background: var(--light-gray);
  z-index: -1;
}

.gene_search_result_multi {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  height: 30px;
}
.gene_search_result_multi_query {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-style: italic;
  flex: 0 0 60px;
  width: 60px;
}
.gene_search_result_multi_query[data-expanded='true'] {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(100% - 30px);
  width: calc(100% - 30px);
}
.gene_search_result_multi_summary {
  display: inline-flex;
  justify-content: flex-start;
  align-items: stretch;
  width: calc(100% - 60px - 30px - 1px - 1px);
}
.gene_search_result_multi_button {
  justify-content: flex-start;
  width: calc((100% - 1px - 1px) / 3);
}
.gene_search_result_multi_check {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 30px;
  width: 30px;
}
.gene_search_result_multi_details {
  text-align: left;
}
.gene_search_result_multi_caret {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 30px;
}

@media screen and (max-width: 500px) {
  .gene_search_result_multi_details:nth-child(2) {
    display: none;
  }
}

.gene_search_results_multi_controls {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 20px;
}

.gene_search_results_multi_alert {
  height: calc(1 * 30px);
  padding: 0 10px;
}


.alert {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 30px;
  color: var(--gray);
}
.alert > svg {
  margin-right: 10px;
}
.alert[data-error='true'] {
  color: var(--red);
}

.table {
  width: 100%;
  max-height: calc((10 * 30px) + ((10 - 1) * 1px));
  overflow-y: auto;
  overflow-x: auto;
  word-break: break-word;
}
.table > * {
  min-width: 500px;
}
.table_head_row {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  align-items: flex-end;
  width: 100%;
  min-height: 30px;
  padding-bottom: 5px;
  border-bottom: solid 1px var(--light-gray);
  background: var(--white);
  z-index: 10;
}
.table_body_row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: 30px;
}
.table_cell {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
}
.table_body_row > .table_cell {
  height: 100%;
}
.table_cell[data-align='left'] {
  justify-content: flex-start;
}
.table_cell[data-align='center'] {
  justify-content: center;
}
.table_cell[data-align='right'] {
  justify-content: flex-end;
}


.gene_selected_controls {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 20px;
}








.detail_row {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.detail_row > span {
  display: inline-block;
  word-break: break-word;
}
.detail_row > span:nth-child(1) {
  flex-basis: 20%;
  flex-grow: 0;
  flex-shrink: 0;
  margin-right: 10px;
}
.detail_row > span:nth-child(2) {
  flex-grow: 1;
}



* {
  --green: #26a36c;
  --light-green: #def9ed;
  --red: #e91e63;
  --black: #000000;
  --dark-gray: #606060;
  --gray: #a0a0a0;
  --light-gray: #e0e0e0;
  --off-white: #f0f0f0;
  --white: #ffffff;

  --shadow: 0 0 4px 0 #00000040;

  --fast: 0.25s ease;

  font-family: 'Poppins';
}
html {
  height: 100%;
}
body {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 11pt;
}
#root {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
}
a {
  position: relative;
  color: var(--green);
}
*:focus {
  outline: solid 1px var(--green);
}
section {
  box-sizing: border-box;
  width: 100%;
  padding: 0 20px;
}
.medium {
  font-weight: 500;
}
.semibold {
  font-weight: 600;
}
.text_small {
  font-size: 9pt;
}
.text_medium {
  font-size: 13pt;
}
.text_large {
  font-size: 18pt;
}
.text_huge {
  font-size: 24pt;
}
.flip_horizontal {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}
.flip_vertical {
  -webkit-transform: scaleY(-1);
          transform: scaleY(-1);
}
*[data-highlight='true'] {
  background: var(--light-green);
}
*[data-outline='true'] {
  outline: solid 1px var(--green);
}
*[data-shade='true'] {
  background: var(--off-white);
}

